body {
  text-align: center;
  margin: 0px;
  padding: 0px;
  font-family: 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -moz-osx-font-smoothing: antialiased;
}

#header {
  height: 120px;
  width: 100%;
  background: white;
  border-bottom: 1px solid #ccc;
  border-top: 3px solid #61dafb;
}

.center {
  width: 75%;
  margin: 0px auto;
}

.clearfix {
  clear: both;
  float: none;
}

#logo {
  width: 25%;
  font-size: 30px;
  float: left;
  margin-top: 40px;
}

#logo img {
  display: block;
  float: left;
  height: 60px;
  margin-top: -10px;
  margin-right: -10px;
  animation: girar infinite 30s linear;
}

@keyframes girar {
  from {
      transform: rotate(0deg);
  }
  to {
      transform: rotate(360deg)
  }
}

#brand {
  display: block;
  margin: 0;
  float: left;
}

#menu {
  width: 75%;
  float: right;
  font-size: 18px;
}

#menu ul {
  line-height: 85px;
  width: 60%;
  float: right;
}

#menu ul li {
  display: inline-block;
  list-style: none;
  height: 46px;
  margin-left: 10px;
  margin-right: 15px;
}

#menu a {
  text-decoration: none;
  color: #444;
  transition: 300ms all;
}

#menu a:hover {
  color: #299ebf;
}

/*Slider*/

#slider {
  width: 100%;
  height: 250px;
  color:white;

  background: rgba(187,218,247,1);
  background: -moz-radial-gradient(center, ellipse cover, rgba(187,218,247,1) 0%, rgba(127,180,240,1) 100%);
  background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%, rgba(187,218,247,1)), color-stop(100%, rgba(127,180,240,1)));
  background: -webkit-radial-gradient(center, ellipse cover, rgba(187,218,247,1) 0%, rgba(127,180,240,1) 100%);
  background: -o-radial-gradient(center, ellipse cover, rgba(187,218,247,1) 0%, rgba(127,180,240,1) 100%);
  background: -ms-radial-gradient(center, ellipse cover, rgba(187,218,247,1) 0%, rgba(127,180,240,1) 100%);
  background: radial-gradient(ellipse at center, rgba(187,218,247,1) 0%, rgba(127,180,240,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#bbdaf7', endColorstr='#7fb4f0', GradientType=1 );
}

#slider h1 {
  margin-top: 0.2px;
  padding: 0px;
  padding-top: 75px;
  text-align: center;
  text-shadow: 0px 0px 5px #444;
}

.btn-white {
  display: block;
  color: rgb(88, 88, 88);
  background: white;
  padding: 10px;
  width: 100px;
  height: 35px;
  margin: 0px auto;
  font-size: 18px;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 35px;
  box-shadow: 0px 0px 5px rgb(88, 88, 88);
  margin-top: 40px;
  border-radius: 4px;
}
.btn-white:hover {
  background: #444;
  color: white;
  transition: 300ms all;
}

/*Slider Blog*/

#slider.slider-small {
  height: 150px;
}

#slider.slider-small h1 {
  margin-top: 0.2px;
  padding: 0px;
  padding-top: 60px ;
}

/*Blog*/

#content {
  width: 70%;
  float: left;
  min-height: 650px;
  margin-right: 20px;
}

#sidebar {
  width: 25%;
  float:left;
  margin-left: 30px;
  margin-top: 50px;
}

.sidebar-item {
  background: #f7f7f7;
  padding: 20px;
  margin-top: 30px;
}

.sidebar-item h3 {
  text-transform: uppercase;
  font-size: 16px;
  margin: 0px;
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 3px solid #ccc;
}

.sidebar-item .btn {
  margin: 0px auto;
  margin-top: 10px;
}

.btn {
  display: block;
  background: black;
  color: white;
  padding: 15px;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 14px;
  text-decoration: none;
  max-width: 120px;
  transition: 300ms all;
  border: none;
  cursor: pointer;
}

.btn-danger{
  background: rgb(255, 0, 0);
  color: white;
  float:left;
  margin-right: 20px;
}

.btn-warning{
  background: rgb(250, 171, 0);
  color: white;
  float:left;
}

.btn-success {
  background: rgb(23, 150, 19);
}

.btn-success:hover {
  background: #000;
}

input[type='text'],
input[type='file'],
textarea {
  width: 100%;
  height: 30px;
  outline: none;
  border: 1 px solid #ccc;
  margin-bottom: 5px;
  padding: 3px;
  transition: 300ms all;
}

input[type='text']:focus,
textarea:focus {
  outline: none;
  box-shadow: 0px 0px 2px #444 inset;
}

input[type='submit'] {
  margin-top: 15px;;
}

/*Footer*/

#footer {
  background: #f7f7f7;
  width: 100%;
  height: 70px;
  line-height: 70px;
  color: #444;
}

#footer p{
  margin: 0px;
  padding: 0px;
}

/*Articulos*/

.article-item {
  width: 100%;
  border-bottom: 1px solid #eee;
  padding-bottom: 20px;
  padding-top: 20px;
  text-align: left;
}

.article-item .image-wrap {
  width: 130px;
  height: 130px;
  overflow: hidden;
  float: left;
  margin-right: 15px;
  border: 1px solid #ccc;
}

.article-item .image-wrap img {
  width:100%;
  height:100%;
}

.article-item h2 {
  display: block;
  width: 100%;
  padding: 0px;
  margin:0px;
  margin-bottom: 7px;
}

.article-item .date {
  display: block;
  width: 100%;
  color: rgb(122,122,122);
}

.article-item a {
  display: block;
  color:#444;
  text-decoration: none;
  margin-top: 10px;
}

.article-item a:hover {
  text-decoration: underline;
}
.subheader {
  font-size: 38px;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
}
.btn-edit {
  margin-top: 10px;
  height: 45px;
}
/*Article p√°gina*/

.article-detail .image-wrap {
  float:none;
  width: 100%;
  height: 200px;
  border: none;
  margin-top: 30px;
}

.article-detail .image-wrap img {
  width: 100%;
  height: auto;
  background-size:cover;

}
.article-item .article-img img{
  max-width: 25%;
  max-height: 150px;
  background-size:cover;
}

.article-detail .subheader {
  margin-top: 15px;
  margin-bottom: 0px;
  border: none;
}

/*Formulario*/

textarea {
  width: 100%;
  height: 150px;
  max-width: 100%;
  min-width: 100%;
  min-height: 150px;
  max-height: 300px;
}

.mid-form {
  width: 50%;
  margin: 0px auto;
  outline: none;
}

.mid-form label {
  display: block;
  text-align: left;
  margin-bottom: 5px;
  margin-top: 10px;
  font-size: 17px;
}

.radio-buttons {
  margin-top: 10px;
  margin-bottom: 10px;
  float: left;
}

.imagEdit {
  display: block;
  width: 180px;
  float: left;
}

                  /*Responsive*/

@media (max-width:1488px) {
  
  .center{
      width: 85%;
  }
}

@media (max-width:1314px) {

  .center {
      width: 95%;
  }

  #menu ul {
      width: auto;
  }
}

@media (max-width:1066px) {
  
  #logo {
      float: none;
      width: 340px;
      margin: 0px auto;
      margin-top: 20px;
  }

  #menu,
  #menu ul {
      clear:both;
      float:none;
      width: 100%;
      margin: 0px;
      padding: 0px;
      line-height: 43px;
  }

  #content {
      float:none;
      width: 100%;
  }

  #sidebar {
      float:none;
      width: 60%;
      margin: 0px auto;
      margin-bottom: 50px;
  }

  #slider h1{
      padding-top: 65px;
      font-size: 25px;
  }

  .mid-form {
      width: 75%;
  }
}

@media (max-width:500px) {

  #header {
      min-height: 130px;
      overflow: hidden;
  }

  .center {
      width: 95%;
  }

  #menu,
  #menu ul {
      line-height: 50px;
      width: auto;
  }    

  #menu ul li {
      margin-left: 7px;
      margin-right: 7px;
  }

  #slider {
      height: 180px;
  }

  #slider h1 {
      font-size: 19px;
      padding-top: 30px;
  }

  #slider .btn-white {
      margin-top: 20px;
      height: 30px;
  }

  #slider.slider-small {
      height: 100px;
  }

  #slider.slider-small h1 {
      padding-top: 35px;
  }
}